html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after{content:'';content:none}q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}.container{width:100%;max-width:500px;margin:0 auto}.container .row{width:90%;margin:0 auto;height:100%;display:flex;justify-content:space-between}.container .row [class*='col-']{min-height:1px}.container .row .col-1{width:8.33333%}.container .row .col-2{width:16.66667%}.container .row .col-3{width:25%}.container .row .col-4{width:33.33333%}.container .row .col-5{width:41.66667%}.container .row .col-6{width:50%}.container .row .col-7{width:58.33333%}.container .row .col-8{width:66.66667%}.container .row .col-9{width:75%}.container .row .col-10{width:83.33333%}.container .row .col-11{width:91.66667%}.container .row .col-12{width:100%}*{box-sizing:border-box}body{font-size:15px;font-family:"Lato",sans-serif;line-height:24px;font-weight:400;background-color:lavenderblush;overflow-x:hidden}a{text-decoration:none}

h1{text-align:center;color:grey;font-size:2em;padding:30px 0;font-family:"Press Start 2P",cursive}main{min-height:calc(100vh - 180px)}main .start{border:3px solid grey;margin:10px 0;border-radius:10px;padding:15px;color:grey}main .start h2{font-size:1.7em;line-height:1.2em}main .start p{margin-top:15px;font-size:1.2em}footer{height:40px;line-height:40px;text-align:center;border-top:1px grey solid;color:grey}footer a{color:grey}footer a:hover{color:black}

header.container .row{justify-content:center}header.container .row .col-9{background-color:white}header.container .row .col-9 label{color:grey}header.container .row .col-9 fieldset{border-color:grey}header.container .row .col-2 span{font-size:1.6em}

main .container{padding-top:20px}main .container .row{flex-direction:column;align-items:center}main .container .row .pokemon{margin:10px 0;border-radius:10px;box-shadow:1px 1px 3px 3px grey;cursor:pointer;background-color:navajowhite}main .container .row .pokemon .pokemon__img{height:200px;background-size:contain;background-position:center;background-repeat:no-repeat;transition:transform 300ms;transform:scale(1.1)}main .container .row .pokemon .pokemon__img:hover{transform:scale(1.2)}main .container .row .pokemon .pokemon__description{padding:0 15px;transform:translateY(-20px)}main .container .row .pokemon .pokemon__description p{text-align:right;color:grey}main .container .row .pokemon .pokemon__description p:nth-of-type(2){font-weight:bold;font-size:2em;margin-bottom:10px;color:black}

.error{border:3px solid red;margin:10px 0;border-radius:10px;padding:15px;box-shadow:1px 1px 3px 3px #fcc}.error h3{font-size:2em;color:red;line-height:1.2em}.error p{color:grey;margin:10px 0;font-size:1.1em}.error p:last-of-type{margin-bottom:0}

main .container .row .pokemon--details{border:1px solid grey;margin:10px 0 20px;border-radius:10px;box-shadow:1px 1px 3px 3px grey;background-color:navajowhite;position:relative;overflow:hidden}main .container .row .pokemon--details .material-icons{font-size:3em;padding:5px;border-right:3px solid grey;border-bottom:3px solid grey;border-radius:5px 0 0 0;cursor:pointer;position:absolute;z-index:10;transition:300ms}main .container .row .pokemon--details .material-icons:hover{color:white;background-color:grey}main .container .row .pokemon--details .pokemon__title{text-align:right;line-height:55px;font-size:2em;border-bottom:3px solid grey;z-index:10}main .container .row .pokemon--details .pokemon__title .pokemon__title__name{font-weight:bold;color:black}main .container .row .pokemon--details .pokemon__title .pokemon__title__id{color:grey;margin:0 15px}main .container .row .pokemon--details .pokemon__img{height:250px;background-size:contain;background-position:center;background-repeat:no-repeat;transition:transform 300ms}main .container .row .pokemon--details .pokemon__img:hover{transform:scale(1.2)}main .container .row .pokemon--details .pokemon__types{text-align:right;font-size:1.5em;transform:translateY(-20px);margin-right:15px}main .container .row .pokemon--details .pokemon__types .pokemon__types__type{padding:0 5px}main .container .row .pokemon--details .pokemon__flavorText{padding:0 15px;text-align:justify;color:grey;font-size:1.2em;margin-bottom:15px}main .container .row .pokemon--details .pokemon__box{border-top:1px solid grey;border-bottom:1px solid grey;box-shadow:1px 1px 3px 3px grey;padding:15px;display:flex;justify-content:space-between}main .container .row .pokemon--details .pokemon__box .pokemon__box__stats{text-align:right;font-size:1.5em;flex:0 0 50%}main .container .row .pokemon--details .pokemon__box .pokemon__box__stats .box__stats__stat{font-size:0.8em;color:grey}main .container .row .pokemon--details .pokemon__box .pokemon__box__stats .box__stats__stat:first-of-type{margin-top:10px;display:block}main .container .row .pokemon--details .pokemon__box .pokemon__box__abilities{text-align:left;font-size:1.5em;flex:0 0 50%}main .container .row .pokemon--details .pokemon__box .pokemon__box__abilities .box__abilities__ability{font-size:0.8em;color:grey}main .container .row .pokemon--details .pokemon__box .pokemon__box__abilities .box__abilities__ability:first-of-type{margin-top:10px;display:block}main .container .row .pokemon--details .pokemon__box .pokemon__box__height,main .container .row .pokemon--details .pokemon__box .pokemon__box__weight{color:grey;font-size:0.8em}main .container .row .pokemon--details .pokemon__box2{padding:15px;display:flex;align-items:center;flex-direction:column}main .container .row .pokemon--details .pokemon__box2 p{font-size:1.5em}main .container .row .pokemon--details .pokemon__box2 p:nth-of-type(2n+2){font-size:1.5em;color:grey;margin-top:10px;display:block}main .container .row .pokemon--details .pokemon__box2 .arrow{position:relative;transform:rotateX(180deg);border:none;margin-top:10px}

